import React,{useState} from 'react'
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import Zoom from '@material-ui/core/Zoom';
import Header from './Header';
import './CreateArea.css';

function CreateArea(props) {
    const [isExpanded, setIsExpanded] = useState(false)
    const [note, setNote] = useState({
        title:"",
        content:""
    })

    function handleChange(event){
        let name = event.target.name;
        let value = event.target.value;

        setNote((prevNote)=>{
            return{
                ...prevNote,
                [name]:value
            }
        })
    }
    
    function onAdded(){
        props.onAdd(note)
        setNote({
            title:'',
            content:''
        })
    }

    function expand(){
        setIsExpanded(true)
    }

    return (
        <div>
             <Header/>
            <form className='create-note'>
                {isExpanded && <input name='title' placeholder='Title' value={note.title} onChange={handleChange}/>}
                <textarea name='content' placeholder='Take a note...' value={note.content} rows={isExpanded ? 3 : 1} onClick={expand} onChange={handleChange}/>
                <Zoom in={isExpanded}>
                <Fab onClick={onAdded}>
                <AddIcon />
                </Fab>
                </Zoom>
            </form>
        </div>
    )
}

export default CreateArea
