import React from 'react'
//import $ from 'jquery'
import DeleteIcon from '@material-ui/icons/Delete';

function Display(props) {

    function handleClick(){
        props.remove(props.id)
    }

    return (
        <div className='note'>
           <h1>{props.title}</h1>
           <p>{props.content}</p>
           <button onClick={handleClick}>
               <DeleteIcon/>
           </button>
        </div>
    )
}

export default Display
